.card {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: auto;
    margin-top: 15%;
    gap: 20px;
    background: #7070cf;
    color: #fff;
    width: 800px;
    padding: 15px 3px;
    border-radius: 25px;
    box-shadow: 3px 7px 18px 0px #000;
}

.header {
    font-size: 2rem;
    text-align: center;
    
}

.line {
    width: 600px;
    height: 1.2px;
    background: #fff;
}

.footer {
    display: flex;
    flex-direction: row;
    /* justif/y-content: space-between; */
    align-items: center;
    gap: 200px;
    font-size: 1.1rem;
    font-weight: 500;
}

.logo {
    display: flex;
    align-items: center;
    gap: 15px;
}

.logo > div > img {
    width: 80px;
    height: 80px;
    cursor: pointer;
}